import React from "react";
import axios from "axios";
import Movie from "./Movie";
import Header from "./Header"
import "./css/App.css"

class App extends React.Component{
  state = {
    isLoading: true,
    movies : []
  }

  getMovies= async() => {
    const { data : { data : { movies }}} = await axios.get("https://yts-proxy.nomadcoders1.now.sh/list_movies.json?sort_by=like_count")
    this.setState({ movies, isLoading: false})
  }

  componentDidMount(){
    this.getMovies();
  }

  render(){
    const { isLoading, movies } = this.state;
    return <section className="container">
      {isLoading ? <div className="loader">
        <span className="loader__text">Loading...</span>
      </div> 
      : <div>
         <Header/>
        <div className="movies">
         {movies.map(movie => (<Movie key = {movie.id} id={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.large_cover_image} genres={movie.genres} />
      ))}
        </div>
      </div>
  }
      </section>
  }
}

export default App;
